.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #1f1f1f;
  padding: 30px;
  border-radius: 8px;
  color: #e0e0e0;
  width: 90%;
  max-width: 400px;
  position: relative;
}
